.welcome
  position: relative
  margin-bottom: 20%

.welcome__plus
  width: 20px
  height: 20px
  position: absolute
  top: 0
  left: 54%
  background: url('../images/plus.svg')
  transform: rotate(7.49deg)

.welcome__circle
  width: 20px
  height: 20px
  position: absolute
  bottom: 31%
  right: 12%
  background: url('../images/circle.svg')
  background-size: cover

.welcome__content
  display: flex
  align-items: center
  justify-content: space-between

.welcome__pic
  width: 659px
  height: 534px
  background-image: url('../images/welcome.png')

.welcome__text
  max-width: 481px

.welcome__title
  font-family: $playfair-bold
  font-size: 36px
  line-height: 50px
  color: #000000
  margin-bottom: 20px

.welcome__subtitle
  font-family: $mulish-regular
  font-size: 17px
  line-height: 30px
  color: #7D7987
  margin-bottom: 39px

.welcome__btn
  width: 164px
  background: $btn-color
  border-radius: 8px
  font-family: $inter-semi-bold
  font-size: 16px
  display: block
  padding: 18px 37px
  color: #FFFFFF
