.stories
  position: relative
  margin-bottom: 15%

.stories__plus 
  width: 20px
  height: 20px
  position: absolute
  top: -10%
  left: 40%
  background: url('../images/plus.svg')
  background-size: cover
  transform: rotate(7.49deg)

.stories__circle1 
  width: 20px
  height: 20px
  position: absolute
  bottom: 27%
  left: 7%
  background: url('../images/circle.svg')
  background-size: cover

.stories__circle2
  width: 20px
  height: 20px
  position: absolute
  bottom: -13%
  right: 36%
  background: url('../images/circle.svg')
  background-size: cover
  
.stories__title

.stories__contant
  max-width: 1122px
  margin: 0 auto

.stories__title-wrap
  display: flex
  align-items: center
  justify-content: space-between
  margin-bottom: 56px

.stories__title
.stories__all
  font-family: $inter-semi-bold
  font-size: 17px
  color: #FB8F1D
  width: 90px
  display: flex
  align-items: center
  &:after
    content: " "
    position: relative
    background: url(../images/arrow-right.png) no-repeat right
    display: block
    height: 25px  
    width: 25px

.stories__cards
  display: flex
  justify-content: space-between

.stories__cardWrap
  padding: 10px 10px 0 10px

.stories__card
  position: relative
  overflow: hidden
  width: 100%
  height: 233px
  margin-bottom: 15px

.stories__pic
  width: 261px
  height: 332px

.stories__img
  position: absolute
  left: 50%
  top: 50%
  transform: translate(-50%, -50%)
  object-fit: cover

.stories__title
  font-family: $inter-bold
  font-size: 19px
  line-height: 30px
  color: #000000
  margin-bottom: 15px

.stories__description
  font-family: $inter-regular
  font-size: 15px
  line-height: 26px
  color: #7D7987
  margin-bottom: 23px
  display: -webkit-box
  -webkit-line-clamp: 3
  -webkit-box-orient: vertical
  overflow: hidden
  text-overflow: ellipsis

.stories__btn
  font-family: $inter-semi-bold
  font-size: 15px
  line-height: 28px
  color: #FB8F1D
