.guides
  position: relative
  max-width: 1205px
  margin: 0 auto
  margin-bottom: 20%

.guides__plus1
  width: 16px
  height: 16px
  position: absolute
  top: 8%
  right: -3%
  transform: rotate(9.99deg)
  background: url('../images/plus.svg')
  background-size: cover

.guides__plus2
  width: 20px
  height: 20px
  position: absolute
  bottom: 19%
  left: 40%
  transform: rotate(20.42deg)
  background: url('../images/plus.svg')
  background-size: cover

.guides__circle
  width: 20px
  height: 20px
  position: absolute
  top: 10%
  left: 5%
  background: url('../images/circle.svg')
  background-size: cover

.guides__content
  display: flex
  align-items: center
  justify-content: space-between

.guides__pic
  width: 599px
  height: 542px
  background-image: url('../images/guides.png')

.guides__text
  max-width: 450px

.guides__title
  font-family: $playfair-bold
  font-size: 36px
  line-height: 50px
  color: #000000
  margin-bottom: 20px

.guides__subtitle
  font-family: $mulish-regular
  font-size: 17px
  line-height: 30px
  color: #7D7987
  margin-bottom: 33px

.guides__btn
  width: 164px
  background: $btn-color
  border-radius: 8px
  font-family: $inter-semi-bold
  font-size: 16px
  display: block
  padding: 18px 43px
  color: #FFFFFF
