.dest
  position: relative
  margin-bottom: 166px

.dest__plus
  width: 20px
  height: 20px
  position: absolute
  top: 70%
  left: 7%
  background: url('../images/plus.svg')

.dest__circle1
  width: 20px
  height: 20px
  position: absolute
  top: -8%
  right: 26%
  background: url('../images/circle.svg')
  background-size: cover

.dest__circle2
  width: 20px
  height: 20px
  position: absolute
  bottom: -11%
  left: 46%
  background: url('../images/circle.svg')
  background-size: cover

.dest__contant
  max-width: 1120px
  margin: 0 auto

.dest__title-wrap
  display: flex
  align-items: center
  justify-content: space-between
  margin-bottom: 56px

.dest__title
  font-family: $playfair-bold
  font-size: 36px
  line-height: 50px
  color: #000000

.dest__all
  font-family: $inter-semi-bold
  font-size: 17px
  color: #FB8F1D
  width: 90px
  display: flex
  align-items: center
  &:after
    content: " "
    position: relative
    background: url(../images/arrow-right.png) no-repeat right
    display: block
    height: 25px  
    width: 25px

.dest__cards
  display: flex
  justify-content: center
  gap: 25px
  
.dest__card
  position: relative

.dest__card-pic
  position: relative
  owerflow: hidden

.dest__card-img
  border-radius: 8px

.dest__card-descr
  background: #fff
  padding-top: 11px
  border-radius: 0 8px 0 8px
  position: absolute
  bottom: 0px
  width: 120px

.dest__card-title
  font-family: $inter-semi-bold
  font-size: 17px
  line-height: 25px
  color: #042521

.dest__card-country
  font-family: $inter-medium
  font-size: 15px
  line-height: 24px
  color: #80918E

