.reviews
  position: relative
  margin-bottom: 15%

.reviews__plus 
  width: 32px
  height: 32px
  position: absolute
  bottom: 0
  right: 37%
  background: url('../images/plus.svg')
  background-size: cover
  transform: rotate(8.17deg)
  z-index: 10

.reviews__circle1 
  width: 42px
  height: 42px
  position: absolute
  top: 16%
  right: 13%
  background: url('../images/circle.svg')
  background-size: cover

.reviews__circle2
  width: 22px
  height: 22px
  position: absolute
  top: 51%
  left: 6%
  background: url('../images/circle.svg')
  background-size: cover

.reviews__content
  max-width: 1180px
  margin: 0 auto
  
.reviews__text
  max-width: 450px

.reviews__title
  margin-bottom: 50px

.reviews__ratings
  display: flex
  max-width: 190px
  justify-content: space-between
  margin-bottom: 30px

.reviews__rating
  width: 25px
  height: 24px
  background-image: url('../images/star.png')
  background-repeat: no-repeat

.reviews__subtitle
  margin-bottom: 40px

.reviews__name
  font-family: $inter-bold
  font-size: 22px
  line-height: 48px
  color: #000000

.reviews__founder
  font-family: $inter-regular
  font-size: 18px
  color: rgba(0, 0, 0, 0.85)

.reviews__slide
  display: flex
  justify-content: space-between
  align-items: center

.reviews__wrap
  padding: 15px

.reviews__pic
  width: 384px
  height: 491px
  position: relative

.reviews__img
  border-radius: 8px

.reviews__nav
  display: flex
  align-items: center
  justify-content: space-evenly
  width: 132px
  height: 60px
  background: #FFFFFF
  border-radius: 16px 0px 0px 0px
  position: absolute
  bottom: 0
  right: 0

.reviews__nav-left
  width: 40px
  height: 40px
  background-image: url('../images/prev.png')
  cursor: pointer

.reviews__nav-right
  width: 40px
  height: 40px
  background-image: url('../images/next.png')
  cursor: pointer

.reviews__left-gray
  width: 40px
  height: 40px
  background-image: url('../images/prev-gray.png')
  cursor: pointer

.reviews__right-gray
  width: 40px
  height: 40px
  background-image: url('../images/next-gray.png')
  cursor: pointer