html {
  // font-family: $font-family-base;
  // font-weight: $font-weight-normal;
  // color: $color-text-base;
}

h1, h2, p {
  margin: 0;
  padding: 0;
}

select {
  appearance: none;
  background-color: transparent;
  border: none;
  outline: none;
}

img {
  width: 100%;
  height: auto;
}

ol, ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

* {
  box-sizing: border-box;
}

.container{
  max-width: 1445px;
  margin: auto;
  padding: 0 15px 0 15px
}




