.header
  position: relative
  margin-bottom: 16%

.header__plus 
  width: 21px
  height: 20px
  position: absolute
  top: 27%
  left: 37%
  background: url('../images/plus.svg')

.header__circle1 
  width: 20px
  height: 20px
  position: absolute
  bottom: 3%
  left: 43%
  background: url('../images/circle.svg')
  background-size: cover

.header__circle2
  width: 26px
  height: 26px
  position: absolute
  bottom: 37%
  left: 2%
  background: url('../images/circle.svg')

.header__content
  display: flex
  align-items: center
  justify-content: space-between
  padding-top: 35px
  margin-bottom: 72px
    
.header__logo
  width: 41px
  height: 41px
  margin-right: auto
    
.header__nav
  
.header__nav-link
  font-family: $inter-medium
  font-size: 16px
  line-height: 19px
  color: $nav-item-color
  margin-right: 45px
  &:last-child
    margin-right: 34px
  &:focus
    color: #202336

.header__buttons
  display: flex
  font-family: $inter-medium
  font-size: 15px

.header__login
  width: 132px
  border: 1px solid $btn-color
  border-radius: 8px
  padding: 15px 0 15px 0
  color: $btn-color
  text-align: center
  margin-right: 18px

.header__register
  width: 132px
  border: 1px solid $btn-color
  border-radius: 8px
  padding: 15px 0 15px 0
  color: #fff
  background-color: $btn-color
  text-align: center

.header__main
  display: flex
  justify-content: space-between
  align-items: center
  
.header__main-text
  flex-basis: 471px

.header__title
  font-family: $playfair-bold
  font-size: 64px
  line-height: 80px
  color: #202336
  margin-bottom: 74px

.header__suptitle
  font-family: $inter-semi-bold
  font-size: 22px
  line-height: 27px
  color: #202336
  margin-bottom: 70px

.header__finder
  display: flex
  flex-wrap: wrap
  justify-content: space-between
  margin-bottom: 12px

.header__finder-select
  width: 220px
  height: 52px
  border: 0.8px solid #AFB0B9
  border-radius: 6px
  margin-bottom: 35px
  font-family: $inter-regular
  font-size: 15px
  line-height: 32px
  color: #4A4C53
  padding-left: 31px
  background: url("../images/arrow.png") no-repeat right
  background-position-x: 90%
  position: relative

.header__finder-option

.header__btn
  width: 180px
  background: $btn-color
  border-radius: 8px
  font-family: $mulish-bold
  font-size: 17px
  padding: 19px 60px
  display: block
  color: #FFFFFF

.header__main-pic
  width: 687px
  height: 654px
  background-image: url("../images/header-main.png")



