.footer
  padding-top: 70px
  background-color: #F9F9FB

.footer__content
  display: flex
  justify-content: space-around
  align-items: center
  padding-bottom: 80px
  border-bottom: 1px solid #C4C4C4

.footer__copyright-wrap
  max-width: 349px

.footer__logo
  width: 44px
  height: 44px
  background-image: url('../images/logo-image.png')
  background-repeat: no-repeat
  margin-bottom: 25px

.footer__text
  font-family: $mulish-light
  font-size: 18px
  line-height: 28px
  color: #848484
  margin-bottom: 55px

.footer__copyryght
  font-family: $mulish-light
  font-size: 17px
  line-height: 28px
  color: #848484

.footer__links-wrap
  display: flex
  justify-content: space-between
  width: 721px

.footer__links-title
  font-family: $mulish-bold
  font-size: 19px
  line-height: 60px
  color: #000000

.footer__links-list

.footer__links-item
  font-family: $mulish-light
  font-size: 18px
  line-height: 38px

.footer__link
  color: #848484

.footer__socials
  display: flex
  justify-content: space-between
  max-width: 240px
  margin: 0 auto
  padding: 30px 0

.footer__social
  width: 20px
  height: 20px

.footer__social-img